<template>
        <!--  -->
        <div class="box">
            <!--  -->
            <div class="sort-top">
                <div class="address">
                    <span></span>
                    <i class="iconfont icon-xialajiantou"></i>
                </div>
                <div class="search">
                    <i class="iconfont iconsousuo"></i>
                </div>
            </div>
            <!--  -->
            <ul class="list">
                <li :class="cate.id ==currentCate?'item active':'item'" v-for="cate in cates" @click="changeCate(cate.id)" :key="cate.id">{{cate.cname}}</li>
            </ul>

            <div class="list-right" style="display:block">
                <van-sticky>
                    <div class="content-top">
                        <span class="heise"></span>
                        <span class="price"><img src="../../style/img/sort/quanhui.png" alt=""><img src="../../style/img/sort/topy.png" class="topy" style="display:none;" alt=""><img src="../../style/img/sort/downy.png" class="downy" style="display:none;" alt=""></span>
                        <span class="top-sort"></span>
                    </div>
               </van-sticky>
                    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                finished-text=""
                                @load="onLoad"
                        >
                            <div class="content-item" v-for="item in goods" :key="item.gid">
                                <router-link :to="{name:'detail',query:{id:item.id}}" class="tupian">
                                    <img  v-lazy="imgName+item.gthumb" alt="">
                                </router-link>
                                <div class="chanpin-information">
                                    <a href="#" class="infor-top">{{item.gname}}</a>
                                    <div class="infor-bottom">
                                        <div class="infor-bottom-left">
                                            {{item.sale}}
                                        </div>
                                        <div class="infor-bottom-right">
                                            <span class="jian"></span>
                                            <span class="num">{{$store.getters.goodsNumber(item.gid)}}</span>
                                            <span class="jia" @click="addGood(item)"></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </van-list>
                    </van-pull-refresh>

            </div>

            <div class="classify ">
                <span class="all "></span>
                <span></span>
                <span></span>
                <span></span>
                <span>/</span>
                <span></span>
            </div>
            <MyFooter :active="1"></MyFooter>
        </div>

</template>

<script>
    import MyFooter from '../../components/my-footer/my-footer'
    import {HOSTNAME,SUCCESS,IMGHOSTNAME} from "../../config/base";
    import axios from 'axios';
    import Vue from 'vue';
    import { Lazyload ,PullRefresh,Sticky} from 'vant';
    import {addGoods} from "../../api/cart";

    Vue.use(Lazyload,PullRefresh,Sticky);
    export default {
        name: "Cate",
        components:{
            MyFooter
        },
        data(){
            return {
                cates:[],
                goods:[],
                current:0,
                limit:6,
                imgName:IMGHOSTNAME,
                currentCate:7,
                loading:false,
                finished:false,
                total:0,
                isLoading:false,
            }
        },
        methods:{
            getCates(){
                axios.get(HOSTNAME+'/api/cates').then(res=>{
                    let data=res.data;
                    if(data.code == SUCCESS){
                        this.cates = data.data;
                        this.currentCate = this.cates[0].id;
                    }
                });
            },
            onRefresh(){
                setTimeout(()=>{
                    this.goods = [];
                    this.current = 0;
                    this.loading=false;
                    this.finished=false;
                    this.isLoading = false;
                    this.getGoods();
                },500);
            },
            getGoods(){
                axios.get(HOSTNAME+'/api/good',{
                    params:{
                        limit:this.limit,
                        page:this.current,
                        cid:this.currentCate
                    }
                }).then(res=>{
                    let data = res.data;
                    this.total = res.total;
                    if(data.code == SUCCESS){
                        this.goods= data.data;
                    }
                });
            },
            changeCate(id){
                this.currentCate = id;
            },
            onLoad(){
              setTimeout(() => {
                    this.current++;
                    axios.get(HOSTNAME+'/api/good',{
                        params:{
                            limit:this.limit,
                            page:this.current,
                            cid:this.currentCate
                        }
                    }).then(res=>{
                        let data = res.data;
                        this.total = data.total;
                        if(data.code == SUCCESS){
                            this.goods.push(...data.data);
                            this.loading = false;
                            // 
                            if (this.goods.length == this.total) {
                                this.finished = true;
                            }
                        }
                    });
                }, 500);
            },
            addGood(item){
                let{gid,sale,gname,gthumb,num}=item;
                addGoods('/api/cart',{gid,sale}).then(res=>{
                    if (res.code == SUCCESS){
                        // this.$store.commit('incGoods',gid);
                        // this.$store.getters.goodsNumber(gid);
                        let {uid,cid} = res.data;
                        this.$store.commit('incGoods',{uid,cid,gid,sale,gname,gthumb,num});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    }
                });
            },

        },
        beforeMount() {
            this.getCates();
        },
        watch:{
            currentCate(){
                this.current = 1;
                this.total=0;
                this.goods=[];
                this.finished = false;
                this.getGoods();
            },
        }
    }
</script>

<style scoped>
@import url("../../style/css/.css");
@import "http://at.alicdn.com/t/font_1342872_sai20ddnxsc.css";
    .active::after{
        content:'';
        width: 0.02rem;
        height: 0.6rem;
        position: absolute;
        left: 0.06rem;
        top: 0.1rem;
        background: #42b983;
    }

</style>
